import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import User from '@/components/User.vue'
import AboutMe from '@/components/AboutMe.vue'
import Login from '@/components/Login.vue'
import UserPower from '@/components/UserPower.vue'
import UserAdd from '@/components/UserAdd.vue'
import UserDelete from '@/components/UserDelete.vue'
import ProjectAll from '@/components/ProjectAll.vue'
import ProjectRab from '@/components/ProjectRab.vue'
import List from '@/components/List.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name:'List',
      component: List
    },
		{
			path: '/user',
			name:'Username',
			component: User
		},
			{
			path: '/about',
			name:'AboutMe',
			component: AboutMe
		},
		{
			path: '/login',
			name:'Login',
			component: Login
		},
		{
			path: '/userpower',
			name:'UserPower',
			component: UserPower
		},
		{
			path: '/useradd',
			name:'UserAdd',
			component: UserAdd
		},
		{
			path: '/userdelete',
			name:'UserDelete',
			component: UserDelete
		},
		{
			path: '/projectall',
			name:'ProjectAll',
			component: ProjectAll
		},
		{
			path: '/projectrab',
			name:'ProjectRab',
			component: ProjectRab
		},
		{
			path: '/list',
			name:'List',
			component: List
		},
  ]
})



