<template>
	<el-container style="height:100%;">

		<el-header style="width: 100%;background-color: white;">
			<el-row>
				<el-col></el-col>
				<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" :router="true"
				 style="width: 100%;">

					<!-- <el-menu-item  class="hidden-md-and-down"></el-menu-item> -->
					<el-menu-item><img src="./assets/logo.png" height="40px" /></el-menu-item>
					<el-menu-item index="/" class="hidden-xs-only">首页</el-menu-item>
				
					<el-menu-item index="about" class="hidden-xs-only">关于我们</el-menu-item>

					<el-menu-item style="float: right;margin-right: 80px;" class="hidden-md-and-down">


						<el-input style="margin-right: 30px;" placeholder="请输入内容" v-model="input" class="input-with-select" clearable>
							<el-button slot="append" icon="el-icon-search"></el-button>
						</el-input>
						<el-dropdown>
							<span class="el-dropdown-link">
								admin<i class="el-icon-arrow-down el-icon--right"></i>
							</span>
							<el-dropdown-menu slot="dropdown">
								<router-link to="/login" style="text-decoration:none"><el-dropdown-item>登录</el-dropdown-item></router-link>
								<el-dropdown-item divided>退出</el-dropdown-item>
							</el-dropdown-menu>
						</el-dropdown>

					</el-menu-item>



				</el-menu>

			</el-row>
			<router-link to="/user"></router-link>

		</el-header>




		<el-container style="height:100%;">


			<AsideList></AsideList>



			<el-main style="margin-left: 290px;  display: block;position: absolute;left: 0;top: 70px;right: 0;bottom: 0;">

				<router-view></router-view>






			</el-main>



		</el-container>


	</el-container>
</template>

<script>
	import Vue from 'vue'
	import Banner from './components/Banner.vue'
	import Aside from './components/Aside.vue'
	import Adver from './components/Adver.vue'
	import AsideList from './components/AsideList.vue'
	import Login from './components/Login.vue'
	export default {
		name: 'App',
		components: {
			Banner,
			Aside,
			AsideList,
			Adver
		},
		data: function() {
			return {
				activeIndex: '/',
				activeIndex2: '1',
				input: ''
			};
		},
		methods: {
			handleSelect(key, keyPath) {
				console.log(key, keyPath);
			},
		


		}

	}
</script>

<style>
	* {
		padding: 0;
		margin: 0;

	}



	.el-header {
		line-height: 60px
	}

	.el-main {
		margin-top: -10px;
	}

	body {
		background: #F5F6F7;
	}

	
</style>



