<template>
	<div style="height:100%">
		<el-card class="box-card" body-style="padding:10px 20px;" style="height:100%" v-loading="loading"
		 element-loading-text="拼命加载中">
			<div slot="header" class="clearfix">
				<div class="block">
					<el-date-picker v-model="timelast" type="daterange" align="right" unlink-panels range-separator="至"
					 start-placeholder="开始日期" end-placeholder="结束日期">
					</el-date-picker>
				</div>





				<div style="float: right;margin-top: -35px;">
					<el-dropdown>
						<el-button type="primary" size="medium">
							操作<i class="el-icon-arrow-down el-icon--right"></i>
						</el-button>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item @click.native="export2Excel()">导出excel</el-dropdown-item>
							<el-dropdown-item @click.native="aclick">调试</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</div>




			</div>

			<!-- {{timelast}} -->
			<el-tabs v-model="activeName" @tab-click="tabClick()">
				<el-tab-pane label="续费" name="续费">
					<el-table :data="xufei.slice((currentPage1-1)*pagesize,currentPage1*pagesize)" height="480px" border
					 highlight-current-row style="width: 100%" v-loading="loading1" element-loading-text="拼命加载中">
						<el-table-column type="index" width="50">
						</el-table-column>
						<el-table-column prop="order_id" label="订单号" width="180">
						</el-table-column>
						<el-table-column prop="product_name" label="产品名称" width="180">
						</el-table-column>
						<el-table-column prop="pay_method" label="支付手段" width="180">
						</el-table-column>
						<el-table-column prop="total_price" label="支付金额" width="180">
						</el-table-column>
						<el-table-column prop="company" label="客户来源">
						</el-table-column>
						<el-table-column prop="iccid" label="用户标识">
						</el-table-column>

					</el-table>
					<div class="block" style="margin-top: 15px;">
						<el-pagination @current-change="handleCurrentChange1" :current-page="currentPage1" :page-size="10" layout="total,  prev, pager, next, jumper"
						 :total="xufei.length">
						</el-pagination>
					</div>
				</el-tab-pane>
				<el-tab-pane label="购买流量" name="购买流量">
					<el-table :data="liuliang.slice((currentPage2-1)*pagesize,currentPage2*pagesize)" height="480px" border
					 highlight-current-row style="width: 100%" v-loading="loading1" element-loading-text="拼命加载中">
						<el-table-column type="index" width="50">
						</el-table-column>
						<el-table-column prop="order_id" label="订单号" width="180">
						</el-table-column>
						<el-table-column prop="product_name" label="产品名称" width="180">
						</el-table-column>
						<el-table-column prop="pay_method" label="支付手段" width="180">
						</el-table-column>
						<el-table-column prop="total_price" label="支付金额" width="180">
						</el-table-column>
						<el-table-column prop="company" label="客户来源">
						</el-table-column>
						<el-table-column prop="iccid" label="用户标识">
						</el-table-column>

					</el-table>
					<div class="block" style="margin-top: 15px;">
						<el-pagination @current-change="handleCurrentChange2" :current-page="currentPage2" :page-size="10" layout="total,  prev, pager, next, jumper"
						 :total="liuliang.length">
						</el-pagination>
					</div>
				</el-tab-pane>
			</el-tabs>


		</el-card>
	</div>
</template>

<script>
	import moment from 'moment'
	import axios from 'axios'
	export default {
		data() {
			return {
				loading: false,
				loading1: false,
				currentPage1: 1, //初始页
				currentPage2: 1,
				pagesize: 10,
				activeName: '续费',
				timelast: '',
				listdata: [],
				listdata2: [],
				selectlist: [],
				xufei: [],
				liuliang: []
			}
		},
		methods: {
			tabClickfun: function() {

			},
			handleCurrentChange1: function(currentPage) {
				this.currentPage1 = currentPage;
			},
			handleCurrentChange2: function(currentPage) {
				this.currentPage2 = currentPage;
			},
			sayhi: function() {
				// 向后端请求数据
				axios.get(
					'static/data.json',
				).then(this.success)
			},
			success: function(res) {
				// const res1 =res.data;
				// console.log(res1['products'][0]['price']);
				res = res.data;
				// this.listdata.push(res);
				this.listdata = res;
				// console.log(this.listdata[0].products[0].name)
				this.twoto1();
			},


			twoto1: function() {
				// 将axios返回的listdata对象转化成elementui表格元件能用的格式,输出结果listdata2
				var i = 0;
				var length = this.listdata.length;
				for (i = 0; i < length; i++) {
					var listdata3 = [];
					listdata3['_id'] = this.listdata[i]['_id'];
					listdata3['order_id'] = this.listdata[i]['order_id'];
					listdata3['product_name'] = this.listdata[i].products[0].name;
					listdata3['product_type'] = this.listdata[i].products[0].type;
					listdata3['pay_method'] = this.listdata[i]['pay_method'];
					listdata3['total_price'] = this.listdata[i]['total_price'];
					listdata3['create_time'] = this.listdata[i]['create_time'];
					listdata3['company'] = this.listdata[i].products[0].company;
					listdata3['iccid'] = this.listdata[i].user.iccid;
					// console.log(this.listdata[i]['products'].name)
					this.listdata2.push(listdata3);

				}


			},

			selectdata: function() {
				// 日期过滤
				this.selectlist = [];
				this.xufei = [];
				this.liuliang = [];
				var i = 0;
				var length = this.listdata2.length;

				for (i = 0; i < length; i++) {
					// console.log(this.listdata2[i]['create_time']);
					// 判断每一个元素的日期是否处于选定的日期区间
					var selectTime1 = moment(this.timelast[0]).format('YYYY-MM-DD HH:mm:ss');
					var selectTime2 = moment(this.timelast[1]).format('YYYY-MM-DD HH:mm:ss');
					var b = moment(this.listdata2[i].create_time);
					var printTime1 = moment(selectTime1);
					var printTime2 = moment(selectTime2);
					var c = moment(b).isBetween(printTime1, printTime2);
					// console.log(c)
					if (c) {
						this.selectlist.push(this.listdata2[i]);
					}
				}
				// console.log(this.selectlist)
			},

			totype: function() {
				// 日期过滤之后,将selectlist中的数据分为续费和购买流量两类，输出结果xufei、liuliang两个数组.
				var i = 0;
				var j = 0;
				var k = 0;
				const length = this.selectlist.length;

				for (i = 0; i < length; i++) {
					if (this.selectlist[i]['product_type'] == 1) {
						this.xufei.push(this.selectlist[i]);

					} else if (this.selectlist[i]['product_type'] == 2) {
						this.liuliang.push(this.selectlist[i]);
					}

				}

			},
			timeout: function(p) {
				// 判断每一个元素的日期是否处于选定的日期区间，是则返回值为true
				// console.log(typeof this.timelast[0]);
				var selectTime1 = moment(this.timelast[0]).format('YYYY-MM-DD HH:mm:ss');
				var selectTime2 = moment(this.timelast[1]).format('YYYY-MM-DD HH:mm:ss');
				var b = moment(this.listdata2[p].create_time);
				var printTime1 = moment(selectTime1);
				var printTime2 = moment(selectTime2);
				var c = moment(b).isBetween(printTime1, printTime2);
				// console.log(c)
				return c;
			},
			aclick: function() {

			},
			tabClick: function() {
				this.loading1 = true;
                    setTimeout(() => {
					
					this.loading1 = false;
				}, 500);
			},
			formatJson: function(filterVal, jsonData) {
				return jsonData.map(v => filterVal.map(j => v[j]))
			},
			export2Excel: function() {
				//导出excel表格
				this.loading = true;
				require.ensure([], () => {
					const {
						export_json_to_excel
					} = require('../vendor/Export2Excel.js');
					const tHeader = ['订单号', '产品名称', '支付手段', '支付金额', '客户来源', '用户标识'];
					const filterVal = ['order_id', 'product_name', 'pay_method', 'total_price', 'company', 'iccid'];
					const list1 = this.xufei;
					const list2 = this.liuliang;
					const data1 = this.formatJson(filterVal, list1);
					const data2 = this.formatJson(filterVal, list2);
					const time1 = moment(this.timelast[0]).format('YYYY-MM-DD');
					const time2 = moment(this.timelast[1]).format('YYYY-MM-DD');
					const title = time1 + ' 至 ' + time2 + ' 产品信息汇总表格'
					// console.log(title);
					setTimeout(() => {
						export_json_to_excel(tHeader, data1, data2, title);
						this.loading = false;
					}, 800);

				})
			}


		},
		mounted() {
			this.sayhi()

		},
		watch: {
			timelast: function() {
				// console.log("值改变了")
				this.loading1 = true;
				setTimeout(() => {
					this.selectdata(); // 日期过滤
					this.totype(); // 日期过滤之后,将selectlist中的数据分为续费和购买流量两类，输出结果xufei、liuliang两个数组.
					this.currentPage1 = 1;
					this.currentPage2 = 1;
					this.loading1 = false;
				}, 500);

			}
		}

	}
</script>

<style>
</style>



