<template>

	<el-row>
		<el-col :span="12" :offset="6">

			<el-card class="box-card">

				<!-- <div slot="header" class="clearfix"> -->
				<span >
					<img style="margin-bottom: -15px;" src="../assets/logo.png" height="40px" />
				</span>
				<!-- </div> -->
				<el-divider>
					<div >
						<img class="box1" style="width: 80px; height: 80px" src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg" />
						<!-- <el-image style="width: 80px; height: 80px" fit="none" src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"
						 ></el-image> -->
					</div>
					
				</el-divider>
				<el-row>

					<el-col :span="16" :offset="4">
						<el-input placeholder="请输入账号" v-model="username" style="margin-top: 30px;margin-bottom: 20px;">
							<template slot="prepend">账号</template>
						</el-input>
						<el-input placeholder="请输入密码" v-model="password" show-password>
							<template slot="prepend">密码</template>
						</el-input>
						<el-button style="width: 100%;margin-top: 20px;margin-bottom: 20px;" type="primary">登录</el-button>
					</el-col>
				</el-row>


			</el-card>
		</el-col>
	</el-row>

</template>

<script>
	
	export default {
		
		data:function(){
			return{
			username:"",
			password:""
			}
		}
	}
</script>

<style>
	.box1 {

		width: 80px;

		height: 80px;

		margin: 30px auto;

		/* border: 1px solid beige; */

		text-align: center;

		line-height: 200px;

		font-weight: bold;

		font-size: 24px;

		background: burlywood;

		border-radius: 50%;

	}
</style>



